import React from 'react';
import DataTable,{createTheme} from 'react-data-table-component';

const datos=[];
const columnas=[
    {
        name:'ID',
        selector:'id',
        sortable: true
    },
    {
        name:'Nombre',
        selector:'nombre',
        sortable: true
    },
];

function consulta() {
    fetch("http://apppemsa.ddns.net:3200/Usuarios")
    .then(res => res.json())
    .then(
        (result) => {
        this.setState({
            isLoaded: true,
            items:this.datos(result)
        });
        console.log(result);
        },
        (error) => {
        this.setState({
            isLoaded: true,
            error
        });
        }
    )
}

const Tabla = () => {
    return (
        <div>
        <DataTable
            columns={columnas}
            data={datos}
            title="personas"
        />
        </div>
    );
}

export default Tabla;
