import React,{Component} from 'react';
import DataTable,{createTheme} from 'react-data-table-component';
const columnas=[
    {
        name:'ID',
        selector:'id',
        sortable: true
    },
    {
        name:'NOMBRE',
        selector:'Nombre',
        sortable: true
    },
    {
        name:'CORREO',
        selector:'Correo',
        sortable: true
    },
    {
        name:'CONTRASEÑA',
        selector:'Contra',
        sortable: true
    },
    {
        name:'TITULAR',
        selector:'titular',
        sortable: true
    },
    {
        name:'FECHA REGISTRO',
        selector:'FechaRegistro',
        sortable: true
    },
    {
        name:'ESTADO',
        selector:'Estado',
        sortable: true
    }
];
class Componente extends Component {
    
    constructor(props) {
        super(props);
        this.state = {
        error: null,
        isLoaded: false,
        items: [],
        dat:[]
        };
    }
    datos(params) {
        let A=[];
        params.forEach(element => {
            console.log(element)
            A.push(element);
        });
        return A;
    }
    componentDidMount() {
        fetch("http://apppemsa.ddns.net:3200/Usuarios")
        .then(res => res.json())
        .then(
            (result) => {
            this.setState({
                isLoaded: true,
                items:this.datos(result)
            });
            },
            (error) => {
            this.setState({
                isLoaded: true,
                error
            });
            }
        )
    }
    
    render() {
        const { error, isLoaded, items } = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (!isLoaded) {
            return <div>Loading...</div>;
        } else {
            return (
                <div>
                    <div>
                    <DataTable
                        columns={columnas}
                        data={items}
                        title="personas"
                    />
                    </div>
                </div>
                
            );
        }
    }

}
export default Componente;